import React, { Component } from 'react'
import { NavHashLink } from 'react-router-hash-link';

export class NavBar extends Component {
  static propTypes = {}

  country = "";

  constructor() {
    super();
    this.state = {
      country: this.country,
    }
  }

  render() {
    return (
      <>
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top" >
          <div className="container-fluid">
            <NavHashLink className="navbar-brand" to="/">News Monkey</NavHashLink>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <NavHashLink className="nav-link active" aria-current="page" to="/">Home</NavHashLink>
                </li>
                
                <li className="nav-item"><NavHashLink className="nav-link" to="/business">Business</NavHashLink></li>
                <li className="nav-item"><NavHashLink className="nav-link" to="/entertainment">Entertainment</NavHashLink></li>
                <li className="nav-item"><NavHashLink className="nav-link" to="/health">Health</NavHashLink></li>
                <li className="nav-item"><NavHashLink className="nav-link" to="/sports">Sports</NavHashLink></li>
                <li className="nav-item"><NavHashLink className="nav-link" to="/science">Science</NavHashLink></li>
                <li className="nav-item"><NavHashLink className="nav-link" to="/technology">Technology</NavHashLink></li>

                {/* <li className="nav-item">
                <a className="nav-link disabled">Disabled</a>
              </li> */}



              </ul>
              {/* <div className="dropdown mx-1">
                <NavHashLink className="btn btn-primary dropdown-toggle " to="/" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  {"Country"}
                </NavHashLink> */}

                {/* <ul className="dropdown-menu">
                  <li><NavHashLink className="dropdown-item" to="/" onClick={() => { this.setState = { country: "in" } }}>India</NavHashLink></li>
                  <li><NavHashLink className="dropdown-item" to="/" onClick={() => { this.setState = { country: "us" } }}>US</NavHashLink></li>
                  <li><NavHashLink className="dropdown-item" to="/" onClick={() => { this.setState = { country: "" } }}>World</NavHashLink></li>
                </ul> */}
              {/* </div> */}
              <form className="d-flex" role="search">
                <input className="form-control mx-2" type="search" placeholder="Search" aria-label="Search" />
                <button className="btn btn-outline-success" type="submit">Search</button>
              </form>
            </div>
          </div>
        </nav>

      </>
    )
  }
}

export default NavBar;

