
import './App.css';
import NavBar from './components/navbar';
import News from './components/news';
import React, { Component } from 'react'
import {
  BrowserRouter
  
} from "react-router-dom";
import { NavHashLink } from 'react-router-hash-link';

export default class App extends Component {
  pageSize=8;
  render() {
    return (
      <>
        <div>
            <NavBar/> 
          <BrowserRouter>
              <NavHashLink exact path="/"element={<News key="home" pageSize={this.pageSize} country="in" category="general" />}/>
              <NavHashLink exact path="/business"element={<News key="business" pageSize={this.pageSize} country="in" category="business" />}/>
              <NavHashLink exact path="/entertainment"element={<News key="entertaiment" pageSize={this.pageSize} country="in" category="entertainment" />}/>
              <NavHashLink exact path="/health"element={<News key="health" pageSize={this.pageSize} country="in" category="health" />}/>
              <NavHashLink exact path="/sports"element={<News key="sports" pageSize={this.pageSize} country="in" category="sports" />}/>
              <NavHashLink exact path="/science"element={<News key="science" pageSize={this.pageSize} country="in" category="science" />}/>
              <NavHashLink exact path="/technology"element={<News key="technology" pageSize={this.pageSize} country="in" category="technology" />}/>
          </BrowserRouter>
        </div >
      </>
    )
  }
}

